import { AbstractColumn } from "../AbstractColumn";
import { Validators } from "@electra/utility";
import { ColumnDefinition } from "../../Definition/ColumnDefinition";
import { IndexDefinition } from "../../Definition/IndexDefinition";
import { ColumnTypeEnum } from "../ColumnTypeEnum";
export class EnumColumn extends AbstractColumn {
    constructor(name, values) {
        super(name);
        this.options = {};
        this.values = values;
        const { valid, message } = Validators
            .all([
            Validators.array(Validators.all([Validators.string(), Validators.minLength(1)])),
            Validators.minLength(1)
        ])
            .validate(this.values);
        if (!valid)
            throw new TypeError(`Invalid ${this.constructor.name} values. ${message}`);
    }
    nullable(nullable = true) {
        const { valid, message } = Validators.boolean().validate(nullable);
        if (valid === false)
            throw new TypeError(`Invalid value passed to EnumColumn.nullable: ${message}`);
        this.options.nullable = nullable;
        return this;
    }
    default(value) {
        const { valid, message } = Validators.string().validate(value);
        const valuesEnum = {};
        for (const value of this.values)
            valuesEnum[value] = value;
        const { valid: enumValueValid, message: enumValueMessage } = Validators.enumValue(valuesEnum).validate(value);
        if (valid === false || enumValueValid === false)
            throw new TypeError(`Invalid value passed to EnumColumn.default: ${message || enumValueMessage}`);
        this.options.default = value;
        return this;
    }
    index(index = true) {
        const { valid, message } = Validators.boolean().validate(index);
        if (valid === false)
            throw new TypeError(`Invalid value passed to EnumColumn.index: ${message}`);
        this.options.index = index;
        return this;
    }
    after(columnName) {
        const { valid, message } = this.validateColumnName(columnName);
        if (valid === false)
            throw new TypeError(`Invalid value passed to EnumColumn.after: ${message}`);
        this.options.after = columnName;
        return this;
    }
    getColumnDefinition() {
        return ColumnDefinition
            .create(this.name, `${ColumnTypeEnum.ENUM}('${this.values.join("', '")}')`)
            .nullable(this.options.nullable)
            .default(this.options.default ? `'${this.options.default}'` : undefined)
            .after(this.options.after);
    }
    getIndexDefinition() {
        if (!this.options.index)
            return null;
        return IndexDefinition
            .create()
            .columns(this.name);
    }
}
